*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}

.card-grid{
  display: flex;

  flex-direction: row;
  flex-wrap: wrap;
}

.card{
 
  border: 1px solid grey ;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 40px;
  margin-right: 10px;
  width: 100%;
  max-width: 250px;
}


.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
 
}
